import { Group, Line, Rect, Text } from 'react-konva';
import TwoTerminalBaseType from './TwoTerminalBaseType';
import { createVoltageGradient, formatPower, voltageToColor } from '../util/Formatting';
import Constants from '../util/Constants';
import Part from '../model/Part';
import { Network } from '../simulation/Network';
import { SystemBuilder } from '../simulation/SystemBuilder';
import PartTerminal from '../model/PartTerminal';
import { NetVariable, PartTerminalVariable } from '../simulation/Variable';
import { Current, NA, Voltage } from '../simulation/PhysicalProperty';
import { Constant } from '../simulation/Value';
import { isNumber } from '../util/Misc';

interface CapacitorProperties {
    capacitance: number;
}

const ctx: CanvasRenderingContext2D = document.createElement('canvas').getContext('2d')!;
const width = 16;
const height = Constants.rendering.twoTerminalPartType.height;
const totalHeight = Constants.rendering.twoTerminalPartType.totalHeight;

class CapacitorType extends TwoTerminalBaseType<CapacitorProperties> {
    constructor() {
        super('capacitor', 2);
    }

    renderSymbol(color: string, terminalVoltages: number[]) {
        const xx = 15;
        const yy = 3;
        return (
            <Group>
                <Line points={[0, -38, 0, -yy]} stroke={color} />
                <Line points={[-xx, -yy, xx, -yy]} stroke={color} />
                <Line points={[-xx, yy, xx, yy]} stroke={color} />
                <Line points={[0, yy, 0, 38]} stroke={color} />
            </Group>
        );
    }

    renderDetails(
        x: number, 
        y: number,
        align: string,
        properties: CapacitorProperties,
        terminalCurrents: number[],
        terminalVoltages: number[]): React.ReactNode {
        const voltageDrop = Math.abs(terminalVoltages[0] - terminalVoltages[1]);
        const current = Math.abs(terminalCurrents[0]);
        const voltsAndAmpsFontSize = Constants.rendering.voltsAndAmpsFontSize;
        return (
            <>
                <Text x={x} y={y} width={100} fontSize={18} align={align} text={properties.capacitance + ' F'} />
                {(current !== null && !isNaN(current)) && current !== 0 &&
                    <Text x={x - 30} y={y - 10} fill='gray' rotation={90} fontSize={voltsAndAmpsFontSize} align={align} text={formatPower(current * voltageDrop)} />
                }
            </>
        );
    }

    createDefaultProperties(): CapacitorProperties {
        return {
            capacitance: 100
        };
    }

    getPropertyLabels() {
        return {
            capacitance: {
                label: 'Capacitance',
                help: 'The capacitance of the capacitor, in Farads'
            }
        };
    }

    getName(): string {
        return 'capacitor';
    }

    buildSystem(part: Part, builder: SystemBuilder): void {
        const pt0 = new PartTerminal(part.id, 0);
        const pt1 = new PartTerminal(part.id, 1);
        const net0 = builder.network.getNetId(pt0);
        const net1 = builder.network.getNetId(pt1);
        const v0 = new NetVariable(Voltage, net0);
        const v1 = new NetVariable(Voltage, net1);
        const current0 = new PartTerminalVariable(Current, pt0);
        const current1 = new PartTerminalVariable(Current, pt1);
        if (net0 !== net1) {
            // I = C * (dv/dt)
            // dv/dt = ((v1 - v0) - (prev1 - prev0)) / deltaT
            // I = C * ((v1 - v0) - (prev1 - prev0)) / deltaT
            // I = C * ((v1 - v0) / deltaT - (prev1 - prev0) / deltaT)
            // I = C * (v1 - v0) / deltaT - C * (prev1 - prev0) / deltaT
            // Q = C * (prev1 - prev0) / deltaT
            // I - C * (v1 - v0) / deltaT = -Q
            // I - C / deltaT * v1 + C / deltaT * v0 = -Q
            const capacitance = (part.properties as CapacitorProperties).capacitance;
            // XXX The net ids may have changed between the solutions, if the schematic was changed
            const previousV0 = builder.previousNetwork && builder.getPreviousValueForVariable(new NetVariable(Voltage, builder.previousNetwork.getNetId(pt0)));
            const previousV1 = builder.previousNetwork && builder.getPreviousValueForVariable(new NetVariable(Voltage, builder.previousNetwork.getNetId(pt1)));
            const previousCurrent = builder.getPreviousValueForVariable(current0);
            if (isNumber(previousV0) && isNumber(previousV1) && isNumber(previousCurrent)) {
                console.log(`previousV0 = ${previousV0}, previousV1 = ${previousV1}, previousCurrent = ${previousCurrent}`);
                const Q: number = capacitance * (previousV1! - previousV0!) / builder.deltaT;
                const cDividedByDeltaT = capacitance / builder.deltaT;
                builder.addRow(
                    [builder.addVariable(current0), builder.addVariable(v1),builder. addVariable(v0)],
                    [1, -cDividedByDeltaT, cDividedByDeltaT],
                    new Constant(Voltage, -Q)
                );
            } else {
                // For initial solution of capacitors, assume resistance of 0.01 ohms (to avoid infinite current).
                // TODO What would be a better solution?
                const resistance = 0.01;
                const inverseR = 1.0 / resistance;
                builder.addRow([builder.addVariable(v0),builder. addVariable(v1),builder. addVariable(current0)], [inverseR, -inverseR, 1], new Constant(NA, 0));
            }
            // Define opposite current on the two terminals
            builder.addRow([builder.addVariable(current0),builder. addVariable(current1)], [1, 1], new Constant(NA, 0));
        } else {
            builder.addRow([builder.addVariable(current0)], [1], new Constant(NA, 0));
            builder.addRow([builder.addVariable(current1)], [1], new Constant(NA, 0));
            // Define equal voltage on the two terminals
            //builder.addRow([builder.addVariable(v0),builder. addVariable(v1)], [1, -1], new Constant(NA, 0));
        }
    }
}

export { CapacitorType, type CapacitorProperties };
