import { calculateLUDecomposition, FloatMatrix, LUDecomposition, mat, vec } from "@josh-brown/vector";
import Part from "../model/Part";
import PartTerminal from "../model/PartTerminal";
import { ResistorType, ResistorProperties } from "../parttype/ResistorType";
import { VoltageSourceType, VoltageSourceProperties } from "../parttype/VoltageSourceType";
import { Network } from "./Network";
import DummyTerminalPart from "../model/DummyTerminalPart";
import { Constant, Value, VariableValue } from "./Value";
import { NetVariable, PartTerminalVariable, Variable, WireVariable } from "./Variable";
import { Voltage, Current, NA } from "./PhysicalProperty";
import { Row, SystemOfEquations } from "./SystemOfEquations";
import { backwardSubstitute, fill, forwardSubstitute, isNumber, NMatrix, NVector } from "../util/Misc";
import { CapacitorProperties, CapacitorType } from "../parttype/CapacitorType";
import Wire from "../model/Wire";
import { SystemBuilder } from "./SystemBuilder";

function formSystemOfEquations(
    deltaT: number,
    parts: Map<number, Part>,
    wires: Map<number, Wire>,
    network: Network, 
    previousNetwork?: Network,
    previousSolution?: SystemOfEquations) {
    const builder = new SystemBuilder(deltaT, network, previousNetwork, previousSolution);

    // Currents - Kirchoff's current law
    network.wireMap.forEach((wireIds, key) => {
        if (wireIds.length > 0) {
            const pt = PartTerminal.parse(key);
            const part = parts.get(pt.partId);
            const dummyPart = part instanceof DummyTerminalPart;
            const variableIndexes: number[] = wireIds.map(wireId =>
                builder.addVariable(new WireVariable(Current, wireId))
            );
            if (!dummyPart) {
                variableIndexes.push(builder.addVariable(new PartTerminalVariable(Current, pt)));
            }
            const variableSigns: number[] = wireIds.map(wireId =>
                wires.get(wireId)!.start.toString() === key ? 1 : -1
            );
            if (!dummyPart) {
                variableSigns.push(1);
            }
            builder.addRow(variableIndexes, variableSigns, new Constant(Current, 0));
        }
    });

    parts.forEach(p => {
        p.type.buildSystem(p, builder);
    });

    //console.log('Coefficients:');
    //rows.forEach(r => console.log(r.coefficients));
    //console.log('Variables:');
    //variables.forEach(v => console.log(v.toString()));
    const system = builder.build();
    return system;
}

function findSingleCoefficientRow(rows: Row[], targetCount: number, predicate?: (row: Row) => boolean): number[] {
    for (var i = 0; i < rows.length; i++) {
        const row = rows[i];
        if (predicate && !predicate(row)) {
            continue;
        }
        const c = row.coefficients;
        var count = 0;
        const cols = [];
        for (var j = 0; j < c.length; j++) {
            if (c[j] !== undefined && c[j] !== null && c[j] !== 0) {
                count++;
                cols.push(j);
            }
        }
        if (count === targetCount) {
            return [i, ...cols];
        }
    }
    return [-1];
}

function doSimplify(system: SystemOfEquations): SystemOfEquations {
    const [rowIndex, colIndex] = findSingleCoefficientRow(system.rows, 1);
    if (rowIndex < 0) {
        return doSimplifyViaRelations(system);
    }

    const coefficient = system.rows[rowIndex].coefficients[colIndex];
    const value = system.rows[rowIndex].value;
    const variable = system.variables[colIndex];
    const variableValue = (value as Constant).constant / coefficient;
    const constant = new Constant(variable.property, variableValue);
    //console.log(`Simplify: Dropping variable ${variable} = ${variableValue}`);
    return system.addVariableRelation(variable, constant);
}

function doSimplifyViaRelations(system: SystemOfEquations): SystemOfEquations {
    const [rowIndex, colIndex1, colIndex2] = findSingleCoefficientRow(system.rows, 2 /*, row => (row.value as Constant).constant === 0*/);
    if (rowIndex < 0) {
        return system;
    }

    const row = system.rows[rowIndex];
    const value = (row.value as Constant).constant;
    const coefficient1 = row.coefficients[colIndex1];
    const coefficient2 = row.coefficients[colIndex2];
    const variable1 = system.variables[colIndex1];
    const variable2 = system.variables[colIndex2];
    const relationValue = new VariableValue(variable1.property, value / coefficient1, -coefficient2 / coefficient1, variable2);

    //console.log(`Simplify: Setting variable ${variable1} = ${relationValue} (row ${rowIndex})`);
    return system.addVariableRelation(variable1, relationValue);
}

function simplify(system: SystemOfEquations, step?: boolean): SystemOfEquations {
    var result = system;
    while (true) {
        const newSystem = doSimplify(result);
        if (newSystem === result) {
            return result;
        }
        result = newSystem;
        if (step) break;
    }
    return result;
}

function doSolve(system: SystemOfEquations): SystemOfEquations {
    console.log(`Solving via LU decomposition, we have ${system.variables} unknowns`)
    const variableCount = system.variables.length;
    const n = Math.max(system.rows.length, variableCount);
    const protoArray = Array(n).fill(0);
    const matrixData = protoArray.map((_, rowIndex) =>
        protoArray.map((_, colIndex) => {
            const row = system.rows[rowIndex];
            return row ? (row.coefficients[colIndex] ?? (colIndex === rowIndex ? 0 : 0)) : 0;
        })
    );
    const matrix: NMatrix = mat(matrixData);
    //console.log(matrix);
    var decomposition: LUDecomposition<number>;
    try {
        decomposition = calculateLUDecomposition(matrix);
    } catch (e) {
        console.log('Failed to do LU decomposition: ' + e);
        return system;
    }
    //console.log(decomposition);

    const b: NVector = vec(fill(system.rows.map(row => (row.value as Constant).constant), n));
    const bMatrix = FloatMatrix.builder().fromColumnVectors([b]);
    const Pb: NVector = decomposition.P.multiply(bMatrix).getColumnVectors()[0];
    const y = forwardSubstitute(decomposition.L, Pb);
    const x = backwardSubstitute(decomposition.U, y);

    var finalSystem = new SystemOfEquations([], [], system.relations);
    system.variables.forEach((v, i) => {
        finalSystem = finalSystem.addVariableRelation(v, new Constant(v.property, x.getEntry(i)));
    });

    return finalSystem;
}

function solve(originalSystem: SystemOfEquations): SystemOfEquations {
    const system = simplify(originalSystem);

    if (system.variables.length != system.rows.length) {
        console.log(`Differing number of variables (${system.variables.length}) and equations (${system.rows.length}); cannot solve!`);
        return system;
    }

    const solution = system.rows.length === 0
        ? system
        : doSolve(system);

    console.log(`Solution: (${solution.rows.length} unknowns remaining)`);
    solution.relations.forEach((value, variable) => {
        console.log(`  ${variable} = ${value.toString()}`);
    });

    return solution;
}

export { formSystemOfEquations, SystemOfEquations, Row, solve, simplify };
