import { EventHandlers } from '../store/MainStore';
import Part from '../model/Part';
import { Vec2 } from '../util';
import { Properties, PropertyLabels } from './PartTypeUtil';
import { Network } from '../simulation/Network';
import { SystemBuilder } from '../simulation/SystemBuilder';

abstract class PartType<P extends Properties> {
    name: string;
    terminalCount: number;

    constructor(name: string, terminalCount: number) {
        this.name = name;
        this.terminalCount = terminalCount;
    }

    abstract getTerminalCoordinates(terminal: number): Vec2;

    abstract render(part: Part, selected: boolean, highlightTerminal: number, eventHandlers: EventHandlers): React.ReactNode;

    abstract createDefaultProperties(): P;

    abstract getPropertyLabels(): PropertyLabels<P>;

    abstract getName(): string;

    abstract buildSystem(part: Part, builder: SystemBuilder): void;
}

export default PartType;
