import Part from "../model/Part";
import Wire from "../model/Wire";
import { fill } from "../util/Misc";
import StringEqMap from "../util/StringEqMap";
import CircuitData from "../util/CircuitData";
import { Network } from "./Network";
import { Constant, Value, VariableValue } from "./Value";
import { Variable } from "./Variable";

// A row in a system of equations
class Row {
    coefficients: number[];
    value: Value;

    constructor(coefficients: number[], value: Value) {
        this.coefficients = coefficients;
        this.value = value;
    }

    empty() {
        return this.coefficients.findIndex(c => c !== undefined && c !== 0) === -1;
    }
}

class SystemOfEquations {
    static Row = Row;

    variables: Variable[];
    rows: Row[];
    relations: StringEqMap<Variable, Value>;

    constructor(variables: Variable[], rows: Row[], relations?: StringEqMap<Variable, Value>) {
        this.variables = variables;
        const n = variables.length;
        this.rows = rows.map(row => 
            new Row(fill(row.coefficients, n), row.value)
        );
        this.relations = relations || new StringEqMap();
    }

    // Updates the model to reflect the currents and voltages represented by this system of equations,
    // by setting the corresponding properties of the parts and wires.
    updateModel(network: Network, parts: Map<number, Part>, wires: Map<number, Wire>) {
        CircuitData.clear();
        const model = { network, parts, wires };
        this.relations.forEach((value: Value, variable: Variable) => {
            if (value instanceof Constant) {
                variable.updateModel(model, value);
            }
        });
    }

    // Returns a new equation system in which the given variable is set equal to the given value.
    // If the value is a constant, the variable is dropped from the equations. If the value refers
    // another variable, the first variable is rewritten in terms of the second variable in the 
    // equations.
    addVariableRelation(variable: Variable, value: Value) {
        if (variable.property !== value.property) {
            // This doesn't currently work - to be able to properly track the units, we would need
            // to accompany each coefficient with a unit, so that we could infer the resulting unit
            // of all multiplications/divisions.
            //throw new Error(`Incompatible units when setting variable ${variable.toString()} = ${value.toString()}`);
        }
        const variableIndex = this.variables.findIndex(v => v.toString() === variable.toString());
        var newVariables = this.variables;
        var newRows: Row[] = this.rows;
        if (variableIndex >= 0) {
            if (value instanceof Constant) {
                newRows = this.rows.map(row => {
                    const c = row.coefficients[variableIndex] ?? 0;
                    const newValue = (row.value as Constant).constant - (c * value.constant);
                    return new Row(
                        row.coefficients.toSpliced(variableIndex, 1),
                        new Constant(row.value.property, newValue)
                    )
                }).filter(row => !row.empty());
                newVariables = this.variables.toSpliced(variableIndex, 1);
            } else if (value instanceof VariableValue) {
                const variableValue = value as VariableValue;
                const dependentVariable = variableValue.variable;
                const dependentVariableIndex = this.variables.findIndex(v => v.toString() === dependentVariable.toString());
                if (dependentVariableIndex >= 0) {
                    newRows = this.rows.map(row => {
                        const existingC = row.coefficients[variableIndex] ?? 0;
                        const c = (existingC * variableValue.coefficient) + 
                            (row.coefficients[dependentVariableIndex] ?? 0);
                        if (c === undefined || isNaN(c)) {
                            throw new Error("!");
                        }
                        return new Row(
                            row.coefficients
                                .toSpliced(dependentVariableIndex, 1, c)
                                .toSpliced(variableIndex, 1),
                            new Constant(row.value.property, (row.value as Constant).constant - (existingC * variableValue.constant))
                        )
                    }).filter(row => !row.empty());
                    newVariables = this.variables.toSpliced(variableIndex, 1);
                }
            } else {
                throw new Error(`Cannot handle value of type ${typeof value}`)
            }

        }

        const newRelations = new StringEqMap(this.relations);
        newRelations.set(variable, value);
        var result = new SystemOfEquations(newVariables, newRows, newRelations);
        if (value instanceof Constant) {
            this.relations.forEach((rValue, rVariable) => {
                if (rValue instanceof VariableValue) {
                    const vv = rValue as VariableValue;
                    if (vv.variable.toString() === variable.toString()) {
                        result = result.addVariableRelation(
                            rVariable, 
                            new Constant(value.property, vv.constant + (value.constant * vv.coefficient))
                        );
                    }
                }
            });
        }

        return result;
    }
}

export { SystemOfEquations, Row };
