type ToString = {
    toString(): string;
}

class StringEqMap<K extends ToString, V> {
    map: Map<String, V>;
    keyMap: Map<String, K>;

    constructor(other?: StringEqMap<K, V>) {
        this.map = other ? new Map(other.map) : new Map();
        this.keyMap = other ? new Map(other.keyMap) : new Map();
    }

    get(key: K): undefined | V {
        return this.map.get(key.toString());
    }

    set(key: K, value: V) {
        this.keyMap.set(key.toString(), key);
        this.map.set(key.toString(), value);
    }

    forEach(callback: (value: V, key: K) => void) {
        this.map.forEach((value, keyString) => {
            const key = this.keyMap.get(keyString);
            callback(value, key!);
        });
    }
}

export default StringEqMap;
