import { PhysicalProperty } from "./PhysicalProperty";
import { Variable } from "./Variable";

abstract class Value {
    property: PhysicalProperty;

    constructor(property: PhysicalProperty) {
        this.property = property;
    }

    abstract toString(): string;
}

class VariableValue extends Value {
    constant: number;
    coefficient: number;
    variable: Variable;

    constructor(property: PhysicalProperty, constant: number, coefficient: number, variable: Variable) {
        super(property);
        this.constant = constant;
        this.coefficient = coefficient;
        this.variable = variable;
    }

    toString(): string {
        return `${this.constant} + (${this.coefficient} * (${this.variable.toString()}))`;
    }
}

class Constant extends Value {
    constant: number;

    constructor(property: PhysicalProperty, constant: number) {
        super(property);
        this.constant = constant;
    }

    toString(): string {
        return `${this.constant} ${this.property}`;
    }
}

export { Value, VariableValue, Constant };
