import PartTerminal from "../model/PartTerminal";
import Wire from "../model/Wire";
import CircuitData from "../util/CircuitData";
import { Model } from "./Model";
import { Current, PhysicalProperty, Voltage } from "./PhysicalProperty";
import { Constant } from "./Value";

abstract class Variable {
    property: PhysicalProperty;

    constructor(property: PhysicalProperty) {
        this.property = property;
    }

    abstract updateModel(model: Model, value: Constant): void;

    abstract toString(): string;
}

class NetVariable extends Variable {
    netId: number;

    constructor(property: PhysicalProperty, netId: number) {
        super(property);
        this.netId = netId;
    }

    updateModel(model: Model, value: Constant): void {
        if (this.property === Voltage) {
            const wireIds: undefined | Set<number> = model.network.netIdToWireMap.get(this.netId) ;
            if (wireIds) {
                wireIds.forEach(wireId => {
                    //console.log(`Setting wire ${wire.id} voltage = ${value.constant}`);
                    //wire.voltage = value.constant;
                    CircuitData.wireVoltages.set(wireId, value.constant);
                });
            }
            const partTerminals = model.network.nets.get(this.netId);
            if (partTerminals) {
                partTerminals.forEach(pt => {
                    CircuitData.partTerminalVoltages.set(pt.toString(), value.constant);
                });
            }
        }
    }

    toString(): string {
        return `<${this.property} of net ${this.netId}>`;
    }
}

class PartTerminalVariable extends Variable {
    partTerminal: PartTerminal;

    constructor(property: PhysicalProperty, partTerminal: PartTerminal) {
        super(property);
        this.partTerminal = partTerminal;
    }

    updateModel(model: Model, value: Constant): void {
        //const terminalCurrents = (window as any).terminalCurrents ||= new Map<string, number>();
        const partTerminal = this.partTerminal;
        const part = model.parts.get(partTerminal.partId);
        if (part) {
            if (this.property === Current) {
                //part.terminalCurrents[partTerminal.terminalId] = value.constant;
                //terminalCurrents.set(partTerminal.toString(), value.constant);
                CircuitData.partTerminalCurrents.set(partTerminal.toString(), value.constant);
            }
        }
    }

    toString(): string {
        return `<${this.property} of part terminal ${this.partTerminal.toString()}>`;
    }
}

class WireVariable extends Variable {
    wireId: number;

    constructor(property: PhysicalProperty, wireId: number) {
        super(property);
        this.wireId = wireId;
    }

    updateModel(model: Model, value: Constant): void {
        //const wireCurrents = (window as any).wireCurrents ||= new Map<number, number>();

        const wireId = this.wireId;
        const wire = model.wires.get(wireId);
        // Check that the wire exists - virtual wires are not part of the circuit (they are added only
        // to the network in the analysis phase to support direct part-to-part connections)
        if (wire) {
            if (this.property === Current) {
                //console.log(`Setting wire ${wireId} current = ${value.constant}`);
                //wire.current = value.constant;
                //wireCurrents.set(wire.id, value.constant);
                CircuitData.wireCurrents.set(wire.id, value.constant);
            } else if (this.property === Voltage) {
                //console.log(`Setting wire ${wireId} voltage = ${value.constant}`);
                //wire.voltage = value.constant;
                CircuitData.wireVoltages.set(wire.id, value.constant);
            }
        }
    }

    toString(): string {
        return `<${this.property} of wire ${this.wireId}>`;
    }
}

export { Variable, NetVariable, PartTerminalVariable, WireVariable };
