import { immerable } from "immer";
import PartType from '../parttype/PartType';
import { Vec2 } from '../util';
import { Properties } from "../parttype/PartTypeUtil";
import Entity from "./Entity";

var idCounter = 0;

class Part extends Entity {
    [immerable] = true

    type: PartType<Properties>;
    x: number;
    y: number;
    flipX: boolean;
    flipY: boolean;
    rotation: number;
    highlightTerminal: number;
    properties: Properties;
    id: number;
    terminalCurrents: number[] = [];
    terminalVoltages: number[] = [];

    constructor(type: PartType<any>, x: number, y: number, flipX?: boolean, flipY?: boolean, rotation?: number) {
        super();
        this.type = type;
        this.x = x;
        this.y = y;
        this.flipX = flipX === undefined ? false : flipX;
        this.flipY = flipY === undefined ? false : flipY;
        this.rotation = rotation === undefined ? 0 : rotation;
        this.highlightTerminal = -1;
        this.properties = type.createDefaultProperties();
        this.id = (idCounter++);
    }

    getTerminalCoordinates(terminal: number): Vec2 {
        const [dx, dy] = this.type.getTerminalCoordinates(terminal);
        return [this.x + dx, this.y + dy];
    }

    rotate() {
        this.rotation += 90;
        if (this.rotation >= 360) {
            this.rotation -= 360;
        }
    }
}

export default Part;
