
class PhysicalProperty {
    name: string;

    constructor(name: string) {
        this.name = name;
    }

    toString(): string {
        return this.name;
    }
}

const Voltage = new PhysicalProperty("voltage");
const Current = new PhysicalProperty("current");
const NA = new PhysicalProperty("n/a");

export { PhysicalProperty, Voltage, Current, NA };
