import { Circle, Group, Line, Text } from 'react-konva';
import Part from '../model/Part';
import PartType from "./PartType";
import { EventHandlers, snap } from '../store/MainStore';
import { Vec2 } from '../util';
import { Properties } from './PartTypeUtil';
import { createVoltageGradient, formatCurrent, formatVoltage } from '../util/Formatting';
import Constants from '../util/Constants';

abstract class TwoTerminalBaseType<P extends Properties> extends PartType<P> {
    render(part: Part, selected: boolean, highlightTerminal: number, eventHandlers: EventHandlers) {
        const color = 'black';
        const textOffsetTable = [[16, -5], [-50, 16], [-50, 5], [-50, -30]];
        const alignTable = ['left', 'center', 'right', 'center'];
        const rotationIndex = Math.round(part.rotation / 90);
        const textOffset = textOffsetTable[rotationIndex];
        const align = alignTable[rotationIndex];
        const voltsAndAmpsFontSize = Constants.rendering.voltsAndAmpsFontSize;
        const arrowSymbol0 = part.terminalCurrents[0] < 0 ? '⇩' : '⇧';
        const arrowSymbol1 = part.terminalCurrents[1] < 0 ? '⇧' : '⇩';
        const circleColor = selected ? Constants.rendering.selectedPartColor : 'transparent';

        return (
            <Group key={part.id} x={part.x} y={part.y} draggable={true}
                onClick={(e) => { eventHandlers.selectPart(part); e.cancelBubble = true; }}
                onDragMove={(e) => {
                    const pos = e.target.position();
                    pos.x = snap(pos.x);
                    pos.y = snap(pos.y);
                    e.target.position(pos);
                    eventHandlers.movePart(part, e.target.x(), e.target.y(), true);
                }}
                onDragEnd={(e) => eventHandlers.movePart(part, e.target.x(), e.target.y(), false)}>
                <Group rotation={part.rotation}>
                    <Group 
                        onDblClick={(e) => eventHandlers.editPart(part)}
                        onDragStart={(e) => { e.target.stopDrag(); e.target.parent?.parent?.startDrag(); }}>
                        <Circle x={0} y={0} radius={24} stroke='transparent' fill={circleColor} />
                        {this.renderSymbol(color, part.terminalVoltages)}
                    </Group>
                    <Circle x={0} y={-40} radius={2} strokeWidth={1} stroke='black' />
                    <Circle x={0} y={40} radius={2} strokeWidth={1} stroke='black' />
                    {!isNaN(part.terminalVoltages[0]) &&
                    <Text x={-33} y={-32} width={30} fill='gray' align='right' fontSize={voltsAndAmpsFontSize} text={formatVoltage(part.terminalVoltages[0])} />}
                    {!isNaN(part.terminalCurrents[0]) &&
                    <Text x={3} y={-32} fill='gray' fontSize={voltsAndAmpsFontSize} text={arrowSymbol0 + ' ' + formatCurrent(Math.abs(part.terminalCurrents[0]))} />}
                    {!isNaN(part.terminalVoltages[1]) &&
                    <Text x={-33} y={26} width={30} fill='gray' align='right' fontSize={voltsAndAmpsFontSize} text={formatVoltage(part.terminalVoltages[1])} />}
                    {!isNaN(part.terminalCurrents[1]) &&
                    <Text x={3} y={26} fill='gray' fontSize={voltsAndAmpsFontSize} text={arrowSymbol1 + ' ' + formatCurrent(Math.abs(part.terminalCurrents[1]))} />}
                    { /* <Text x={-6} y={0} text={String(part.id)} /> */ }
                    <Line points={[0, -40, 0, 40]} stroke='transparent' strokeWidth={5} listening={false} dash={[5, 10, 5, 10]} name='wire' id={'p' + part.id} />
                    <Circle radius={5} x={0} y={-40}
                        fill={highlightTerminal === 0 ? 'red' : 'transparent'}
                        onClick={(e) => { eventHandlers.clickTerminal(part, 0); e.cancelBubble = true; }}
                        onMouseOver={(e) => eventHandlers.hoverTerminal(part, 0)}
                        onMouseOut={(e) => eventHandlers.hoverTerminal(part)} />
                    <Circle radius={5} x={0} y={40}
                        fill={highlightTerminal === 1 ? 'red' : 'transparent'}
                        onClick={(e) => { eventHandlers.clickTerminal(part, 1); e.cancelBubble = true; }}
                        onMouseOver={(e) => eventHandlers.hoverTerminal(part, 1)}
                        onMouseOut={(e) => eventHandlers.hoverTerminal(part)} />
                </Group>
                {this.renderDetails(textOffset[0], textOffset[1], align, part.properties as any, part.terminalCurrents, part.terminalVoltages)}
            </Group>
        );
    }

    getTerminalCoordinates(terminal: number): Vec2 {
        if (terminal === 0) {
            return [0, -40];
        } else {
            return [0, 40];
        }
    }

    abstract renderSymbol(color: string, terminalVoltages: number[]): React.ReactNode;

    abstract renderDetails(
        x: number,
        y: number,
        align: string,
        properties: P,
        terminalCurrents: number[],
        terminalVoltages: number[]): React.ReactNode;
}

export default TwoTerminalBaseType;
