import { mat, vec, Matrix, Vector } from '@josh-brown/vector';
import DummyTerminalPart from "./model/DummyTerminalPart";
import Part from "./model/Part";
import Wire from "./model/Wire";
import PartTerminal from './model/PartTerminal';
import { isNumber } from './util/Misc';

type Vector2d = Vector<number>;
type Vec2 = [number, number];

function buildPartIdToWiresMap(wires: Map<number, Wire>) {
    const partToWires = new Map<number, Wire[]>(); // Keys are part ids
    const addWire = (partId: number, w: Wire) => {
        const existing = partToWires.get(partId);
        if (existing === undefined) {
            partToWires.set(partId, [w]);
        } else {
            existing.push(w);
        }
    };
    Array.from(wires.values()).forEach((w) => {
        addWire(w.start.partId, w);
        addWire(w.end.partId, w);
    });
    return partToWires;
}

function dropExtraDummyTerminals(parts: Map<number, Part>, wires: Map<number, Wire>) {
    const dummyCoordinateMap = new Map<string, Part>();
    const nonDummies: Part[] = [];

    Array.from(parts.values()).forEach((p) => {
        if (p instanceof DummyTerminalPart) {
            dummyCoordinateMap.set(p.x + ',' + p.y, p);
        } else {
            nonDummies.push(p);
        }
    });

    const partIdToWiresMap: Map<number, Wire[]> = buildPartIdToWiresMap(wires);

    Array.from(parts.values()).forEach((part) => {
        for (var t = 0; t < part.type.terminalCount; t++) {
            const [x, y] = part.type.getTerminalCoordinates(t);
            const dummy = dummyCoordinateMap.get((part.x + x) + ',' + (part.y + y));
            if (dummy && dummy.id !== part.id) {
                console.log(`Found dummy at ${x}, ${y}`)
                const wiresToPart = partIdToWiresMap.get(dummy.id);
                if (wiresToPart !== undefined && wiresToPart.length > 0) {
                    for (let w of wiresToPart) {
                        if (w.start.partId === dummy.id) {
                            w.start = new PartTerminal(part.id, t);
                        }
                        if (w.end.partId === dummy.id) {
                            w.end = new PartTerminal(part.id, t);
                        }
                        parts.delete(dummy.id);
                    }
                }
            }
        }
    });

    dropUnconnectedDummyTerminals(parts, wires);
}

function dropWiresWithSameStartAndTerminal(wires: Map<number, Wire>) {
    Array.from(wires.values()).forEach((w) => {
        if (w.start.equals(w.end)) {
            console.log(`Deleting wire ${w.id}`);
            wires.delete(w.id);
        }
    })
}

function dropUnconnectedDummyTerminals(parts: Map<number, Part>, wires: Map<number, Wire>) {
    dropWiresWithSameStartAndTerminal(wires);

    const partIdToWiresMap: Map<number, Wire[]> = buildPartIdToWiresMap(wires);

    // Delete dummies without any wires connected to them
    Array.from(parts.values()).forEach((part) => {
        if (part instanceof DummyTerminalPart) {
            const wires = partIdToWiresMap.get(part.id);
            if (!wires?.length) {
                parts.delete(part.id);
            }
        }
    });
}

function getWireDisconnectArrowCoordinates(startPoint: Vector2d, endPoint: Vector2d ): number[] {
    const v = endPoint.add(startPoint.scalarMultiply(-1));
    const unit = v.scalarMultiply(1 / Math.sqrt(v.innerProduct(v)));
    const perpUnit = vec([unit.getEntry(1), -unit.getEntry(0)]);
    const p1 = endPoint.add(unit.scalarMultiply(-20));
    const p2 = endPoint.add(unit.scalarMultiply(-10)).add(perpUnit.scalarMultiply(7));
    const p3 = endPoint.add(unit.scalarMultiply(-10)).add(perpUnit.scalarMultiply(-7));
    return [...p1.toArray(), ...p2.toArray(), ...p3.toArray()];
}

function _getDashOffset(current: number): number {
    if (!isNumber(current)) {
        return 0;
    }
    // Drop some higher bits to make the division on the next line more stable with small currents
    const time = Date.now() % 3000;
    const factor = (time / (5 / Math.abs(current))) % 30;
    const offset = current < 0 ? (30 - factor) : factor;
    return offset;
}

function getDashOffset(current: number, previousOffset?: number): number {
    if (!isNumber(current)) {
        return 0;
    }
    const newOffset = (previousOffset || 0) + (current * 20);
    return newOffset % 30;
}

export type { Vector2d, Vec2 };
export { dropExtraDummyTerminals, dropUnconnectedDummyTerminals, getWireDisconnectArrowCoordinates, getDashOffset };
