import DummyTerminalPart from "../model/DummyTerminalPart";
import Part from "../model/Part";
import PartTerminal from "../model/PartTerminal";
import Wire from "../model/Wire";
import { Vec2 } from "../util";

class Network {
    nets: Map<number, PartTerminal[]>;  // Keys are net ids, values are part terminal arrays of that net
    netMap: Map<string, number>;  // Keys are part-terminal string, values are net ids
    wireMap: Map<string, number[]>;  // Keys are part-terminal strings, values are ids of wires connected to that part-terminal
    wireToNetIdMap: Map<number, number>; // Keys are wire ids, values are net ids
    netIdToWireMap: Map<number, Set<number>>;  // Keys are net ids, values are wire ids connected to that net
    netId = 1;

    constructor(nets: PartTerminal[][], wireMap: Map<string, number[]>) {
        this.nets = new Map();
        this.wireMap = wireMap;
        this.netMap = new Map();
        this.wireToNetIdMap = new Map();
        this.netIdToWireMap = new Map();
        nets.forEach(net => {
            const id = this.netId++;
            this.nets.set(id, net);
            net.forEach(pt => {
                this.netMap.set(pt.toString(), id);
                const wires = wireMap.get(pt.toString()) ?? [];
                wires.forEach(wireId => {
                    this.wireToNetIdMap.set(wireId, id);
                    const netWires = this.netIdToWireMap.get(id) ?? new Set();;
                    netWires.add(wireId);
                    this.netIdToWireMap.set(id, netWires);
                });
            });
        });
    }

    getNetId(partTerminal: PartTerminal): number {
        var netId = this.netMap.get(partTerminal.toString());
        if (!netId) {
            // This happens for part-terminals that have no wires connected.
            // We assign a new net id here.
            netId = this.netId++;
            this.netMap.set(partTerminal.toString(), netId);
            this.nets.set(netId, [partTerminal]);
        }
        return netId;
    }

    hasWires(pt: PartTerminal) {
        const wires = this.wireMap.get(pt.toString());
        return wires !== undefined && wires.length > 0;
    }

   log() {
        console.log('Connections:');
        Array.from(this.nets.values()).forEach((net, i) => {
            const str = net.join(', ');
            console.log(`  Net ${i + 1}: ${str}`);
        });
    }
}

// Builds a map of overlapping part terminal, to support connecting part terminals
// directly to other part terminals, without a wire.
function buildPartTerminalPositionMap(parts: Map<number, Part>) {
    const partTerminalPositionMap: Map<string, PartTerminal[]> = new Map();
    parts.forEach(part => {
        const n = part.type.terminalCount;
        for (let i = 0; i < n; i++) {
            const pos: Vec2 = part.getTerminalCoordinates(i);
            const posString = `${pos[0]}_${pos[1]}`;
            const partTerminals = partTerminalPositionMap.get(posString) || [];
            partTerminals.push(new PartTerminal(part.id, i));
            partTerminalPositionMap.set(posString, partTerminals);
        }
    });
    return partTerminalPositionMap;
}

function addVirtualWires(partTerminalPositionMap: Map<string, PartTerminal[]>, wires: Map<number, Wire>) {
    partTerminalPositionMap.forEach(partTerminals => {
        if (partTerminals.length > 1) {
            const pt0 = partTerminals[0];
            for (let i = 1; i < partTerminals.length; i++) {
                const pt1 = partTerminals[i];
                const wire = new Wire(pt0, pt1);
                wires.set(wire.id, wire);
            }
        }
    });
}

function buildNetwork(parts: Map<number, Part>, realWires: Map<number, Wire>): Network {
    const partTerminalPositionMap: Map<string, PartTerminal[]> = buildPartTerminalPositionMap(parts);
    const wires = new Map(realWires);
    addVirtualWires(partTerminalPositionMap, wires);

    const nets: Map<string, PartTerminal[]> = new Map();
    // Direct connections from part-terminals to wires
    const wireMap: Map<string, number[]> = new Map();
    wires.forEach(w => {
        const startKey = w.start.toString();
        const endKey = w.end.toString();

        const startArr = nets.get(startKey) || [];
        const endArr = nets.get(endKey) || [];

        const newArr = startArr.concat(endArr);

        if (startArr.length === 0) {
            newArr.push(w.start);
        }
        if (endArr.length === 0) {
            newArr.push(w.end);
        }
        newArr.forEach((pt) =>
            nets.set(pt.toString(), newArr)
        );

        const startWires = wireMap.get(w.start.toString()) || [];
        startWires.push(w.id);
        wireMap.set(w.start.toString(), startWires);

        const endWires = wireMap.get(w.end.toString()) || [];
        endWires.push(w.id);
        wireMap.set(w.end.toString(), endWires);
    });

    /* This is maybe not necessary after all?
    while (true) {
        var filterCount = 0;
        wireMap.entries().forEach(([keyString, wireIds]) => {
            // Filter out unconnected wires
            const filteredWires = wireIds.filter(wireId => {
                const wire = wires.get(wireId)!;
                const otherEnd = wire.start.toString() === keyString ? wire.end : wire.start;
                const otherEndPart = parts.get(otherEnd.partId);
                const otherEndWires = wireMap.get(otherEnd.toString());
                const filterOut = otherEndPart instanceof DummyTerminalPart && otherEndWires?.length === 1;
                return !filterOut;
            });
            if (filteredWires.length < wireIds.length) {
                wireMap.set(keyString, filteredWires);
                filterCount++;
            }
        });
        if (filterCount === 0) break;
    }
    */

    const uniqueNets = new Set<PartTerminal[]>(
        Array.from(nets.values())
    );
    const dummiesFilteredOut = Array.from(uniqueNets); /*.map(pts =>
        pts.filter(pt => !(parts.get(pt.partId) instanceof DummyTerminalPart))
    ).filter(pts => pts.length > 0);*/

    return new Network(dummiesFilteredOut, wireMap);
}

export { Network, buildNetwork as buildNet };
