class  PartTerminal {
    partId: number;
    terminalId: number;

    constructor(partId: number, terminalId: number) {
        this.partId = partId;
        this.terminalId = terminalId;
    }

    equals(other: PartTerminal): boolean {
        return this.partId === other.partId && this.terminalId === other.terminalId;
    }

    toString() {
        return this.partId + '_' + this.terminalId;
    }

    static parse(str: string) {
        const match = /([0-9]+)_([0-9]+)/.exec(str);
        if (!match) {
            throw new Error("Failed to parse part terminal string: " + str);
        }
        return new PartTerminal(parseInt(match[1]), parseInt(match[2]));
    }
}

export default PartTerminal;
