import { Circle, Group } from 'react-konva';
import { Vec2 } from '../util';
import Part from '../model/Part';
import PartType from "./PartType";
import { EventHandlers } from '../store/MainStore';
import { Properties, PropertyLabels } from './PartTypeUtil';
import { SystemBuilder } from '../simulation/SystemBuilder';

interface DummyTerminalProperties {
}

class DummyTerminalType extends PartType<DummyTerminalProperties> {
    constructor() {
        super('_dummy_', 1);
    }

    getTerminalCoordinates(terminal: number): Vec2 {
        return [0, 0];
    }

    render(part: Part, selected: boolean, highlightTerminal: number, eventHandlers: EventHandlers) {
        return (
            <Group _useStrictMode key={part.id} x={part.x} y={part.y} draggable={true}
                onClick={() => eventHandlers.selectPart(part)}
                onDragMove={(e) => eventHandlers.movePart(part, e.target.x(), e.target.y(), true)}
                onDragEnd={(e) => eventHandlers.movePart(part, e.target.x(), e.target.y(), false)}>
                <Circle radius={5} x={0} y={0}
                    fill={highlightTerminal == 0 ? 'red' : 'transparent'}
                    onClick={(e) => { eventHandlers.clickTerminal(part, 0); e.cancelBubble = true; }}
                    onMouseOver={(e) => eventHandlers.hoverTerminal(part, 0)}
                    onMouseOut={(e) => eventHandlers.hoverTerminal(part, -1)} />
            </Group>
        );
    }

    createDefaultProperties(): DummyTerminalProperties {
        return {};
    }

    getPropertyLabels(): PropertyLabels<DummyTerminalProperties> {
        return {};
    }

    getName(): string {
        return 'dummy';
    }

    buildSystem(part: Part, builder: SystemBuilder): void {
    }
}

export default DummyTerminalType;
