import { Network } from "./Network";
import { Row, SystemOfEquations } from "./SystemOfEquations";
import { Constant, Value } from "./Value";
import { Variable } from "./Variable";

class SystemBuilder {
    variables: Variable[] = [];
    variableIndexMap: Map<string, number> = new Map();
    rows: Row[] = [];
    deltaT: number;
    network: Network;
    previousNetwork?: Network;
    previousSolution?: SystemOfEquations;

    constructor(deltaT: number, network: Network, previousNetwork?: Network, previousSolution?: SystemOfEquations) {
        this.deltaT = deltaT;
        this.network = network;
        this.previousNetwork = previousNetwork;
        this.previousSolution = previousSolution;
    }

    addVariable(v: Variable): number {
        var index = this.variableIndexMap.get(v.toString());
        if (index === undefined) {
            index = this.variables.length;
            this.variables.push(v);
            this.variableIndexMap.set(v.toString(), index);
        }
        return index;
    }

    addRow(indexes: number[], coefficients: number[], value: Value): void {
        if (indexes.length !== coefficients.length) {
            throw new Error(`Got ${indexes.length} indexes but ${coefficients.length} coefficients`);
        }
        const finalCoefficients: number[] = [];
        coefficients.forEach((c, i) => {
            finalCoefficients[indexes[i]] = c;
        });
        this.rows.push(
            new Row(finalCoefficients, value)
        );
    }

    getPreviousValueForVariable(variable: Variable): null | number {
        if (this.previousSolution) {
            const value = this.previousSolution.relations.get(variable);
            if (value && value instanceof Constant) {
                return value.constant;
            }
        }
        return null;
    }
    
    build(): SystemOfEquations {
        return new SystemOfEquations(this.variables, this.rows);
    }
}

export { SystemBuilder };
