import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { EventHandlers } from '../store/MainStore';
import { Dropdown, DropdownButton, Form } from 'react-bootstrap';

function renderMenuBar(run: boolean, eventHandlers: EventHandlers) {
    return (
        <Navbar collapseOnSelect fixed="top" className="bg-body-tertiary" style={{borderBottom: '1px solid lightgray'}}>
            <Nav style={{ marginLeft: 20 }}>
                <DropdownButton id="dropdown-basic-button" title="Add...">
                    <Dropdown.Item onClick={e => eventHandlers.addPart('resistor')}>Resistor</Dropdown.Item>
                    <Dropdown.Item onClick={e => eventHandlers.addPart('capacitor')}>Capacitor</Dropdown.Item>
                </DropdownButton>
                <Form.Check type='switch' label='Run' defaultChecked={run} onClick={e => eventHandlers.pauseOrResume()} style={{margin: 8}} />
            </Nav>
        </Navbar>
    );
}

export default renderMenuBar;
