import Konva from "konva";
import CircuitData from "./CircuitData";
import { isNumber } from "./Misc";
import { createVoltageGradient, formatCurrent, formatCurrentAndArrow, formatVoltage, voltageToColor } from "./Formatting";
import { getDashOffset } from "../util";

function updateCurrentFlow(stage: Konva.Stage) {
    const wires = stage.find('.wire');
    const currents = [];
    for (let wireLine of wires) {
        //console.log(`  Found wire: ${(wireLine as any).id()}`);
        const id: string = (wireLine as any).id();
        var current;
        if (id.startsWith('p')) {
            // Current flow through a part
            const partId = parseInt(id.substring(1));
            current = CircuitData.partTerminalCurrents.get(`${partId}_0`);
            const gradient = createVoltageGradient(
                CircuitData.partTerminalVoltages.get(`${partId}_0`) || 0,
                CircuitData.partTerminalVoltages.get(`${partId}_1`) || 0,
                0, -40, 0, 40
            );
            (wireLine as any).stroke(gradient);
        } else {
            const wireId = parseInt(id);
            current = CircuitData.wireCurrents.get(wireId);
            const voltage = CircuitData.wireVoltages.get(wireId);
            (wireLine as any).stroke(voltageToColor(voltage || 0));
        }
        if (current) {
            currents.push(current);
            //console.log(`  ${id} = ${current}`);
            const offset = getDashOffset(current, (wireLine as any).dashOffset());
            (wireLine as any).dashOffset(offset);
        }
    }
}

function updateWireVoltages(stage: Konva.Stage) {
    const elems = stage.find('.wireVoltage');
    for (let elem of elems) {
        const id: string = (elem as any).id();
        const voltage = CircuitData.wireVoltages.get(parseInt(id)) || 0;
        (elem as any).text(formatVoltage(voltage));
    }
}

function updateWireCurrents(stage: Konva.Stage) {
    const elems = stage.find('.wireCurrent');
    for (let elem of elems) {
        const idString: string = (elem as any).id();
        const flipArrow = idString.startsWith('_');
        const id = parseInt(idString.startsWith('_') ? idString.substring(1) : idString);
        const current = CircuitData.wireCurrents.get(id) || 0;
        (elem as any).text(formatCurrentAndArrow(current, flipArrow));
    }
}

export { updateCurrentFlow, updateWireVoltages, updateWireCurrents };
