import { Layer } from "react-konva";
import { State, useStore } from "../store/MainStore";
import renderWire from "./SvgWire";
import renderPart from "./SvgPart";
import renderSolution from "./SvgSolution";

const SvgCircuit = () => {
    console.log('SvgCircuit');
    const { parts, wires, system, selectedWireId, selectedPartId, hoveredPartId, highlightTerminal, eventHandlers }: State = useStore(state => 
        ({ 
            parts: state.parts,
            wires: state.wires,
            system: state.system,
            selectedWireId: state.selectedWireId,
            selectedPartId: state.selectedPartId,
            hoveredPartId: state.hoveredPartId,
            highlightTerminal: state.highlightTerminal,
            eventHandlers: state.eventHandlers
        })
    );

    return (
        <>
            <Layer name='wireLayer'>
                {Array.from(wires.values()).map((w) =>
                    renderWire(
                        parts,
                        w,
                        selectedWireId === w.id,
                        eventHandlers
                    )
                )}
            </Layer>
            <Layer>
                {Array.from(parts.values()).map((p) =>
                    renderPart(
                        p,
                        p.id === selectedPartId,
                        p.id === hoveredPartId,
                        highlightTerminal === undefined ? -1 : highlightTerminal,
                        eventHandlers
                    )
                )}
            </Layer>
            <Layer x={500} y={100}>
                {/* state.solution && renderSolution(state.solution) */}
            </Layer>
        </>
    );
};

export default SvgCircuit;
