import React from 'react';
import { Stage } from 'react-konva';
import { State, temporalProperties, useStore } from './store/MainStore';
import Konva from 'konva';
import renderMenuBar from './menu/MenuBar';
import SvgCircuit from './svg/SvgCircuit';
import PropertyDialog from './PropertyDialog';
import { updateCurrentFlow, updateWireCurrents, updateWireVoltages } from './util/Animation';

const App = () => {
    const { parts, run, eventHandlers }: State = useStore(state =>
        ({ parts: state.parts, run: state.run, eventHandlers: state.eventHandlers })
    );
    const { undo, redo } = temporalProperties.getState();
    const onKeyDown = (event: React.KeyboardEvent) => {
        if (!event.altKey && !event.shiftKey && !event.ctrlKey && event.metaKey && event.key == 'z') {
            console.log('undo');
            undo();
            eventHandlers.start();
        } else if (!event.altKey && event.shiftKey && !event.ctrlKey && event.metaKey && event.key == 'z') {
            redo();
        } else if (!event.altKey && !event.shiftKey && !event.ctrlKey && !event.metaKey) {
            if (event.key === 'Backspace') {
                eventHandlers.delete();
            } else if (event.key === ' ') {
                eventHandlers.simplify();
            } else if (event.key === '.') {
                eventHandlers.step();
            }
        }
    };

    const stageRef = React.useRef(null);
    React.useEffect(() => {
        if (stageRef.current) {
            const stage = stageRef.current as Konva.Stage;
            const currentLayer = stage.find('.currentLayer');
            const animation = new Konva.Animation(frame => {
                updateCurrentFlow(stage);
                updateWireVoltages(stage);
                updateWireCurrents(stage);
            }, currentLayer);
            animation.start();
            const interval = setInterval(() => {
                if (run) {
                    eventHandlers.step();
                }
            }, 5);
            return () => {
                animation.stop();
                clearInterval(interval);
            };
        }
    });

    console.log(`Rendering App`);
    const paddingTop = 55;
    return (
        <div style={{ paddingTop }}>
            {renderMenuBar(run, eventHandlers)}
            <PropertyDialog />
            <div onKeyDown={onKeyDown} tabIndex={0}>
                <Stage width={window.innerWidth} height={window.innerHeight - paddingTop}
                    ref={stageRef}
                    onMouseMove={e => {
                        if (eventHandlers.mouseMoveHandler) {
                            eventHandlers.mouseMoveHandler(e.evt.layerX, e.evt.layerY);
                        }
                    }}
                    onClick={e => {
                        eventHandlers.click(e.evt.clientX, e.evt.clientY);
                    }}>
                    <SvgCircuit />
                    {/* <SvgCurrents /> */}
                </Stage>
            </div>
        </div>
    );
}

export default App;
