import isDeepEqual from 'fast-deep-equal/es6/react';
import { create } from 'zustand';
import { useShallow } from 'zustand/react/shallow';
import { temporal } from "zundo";
import { produce, enableMapSet } from 'immer';
import Part from "../model/Part";
import Wire, { StartOrEnd } from "../model/Wire";
import { ResistorProperties, ResistorType } from "../parttype/ResistorType";
import DiodeType from "../parttype/DiodeType";
import { dropExtraDummyTerminals } from '../util';
import DummyTerminalPart from '../model/DummyTerminalPart';
import { VoltageSourceType } from '../parttype/VoltageSourceType';
import PartTerminal from '../model/PartTerminal';
import { buildNet, Network } from '../simulation/Network';
import { SystemOfEquations, formSystemOfEquations, simplify, solve } from '../simulation/Solver';
import { CapacitorType } from '../parttype/CapacitorType';

enableMapSet();

interface EventHandlers {
    start: () => void;
    pauseOrResume: () => void;
    click: (x: number, y: number) => void;
    addPart: (type: string) => void;
    selectPart: (part: Part) => void;
    editPart: (part?: Part) => void;
    setPartProperty: (partId: number, property: string, newValue: string) => void;
    movePart: (part: Part, x: number, y: number, draft: boolean) => void;
    hoverTerminal: (part: Part, terminal?: number) => void;
    clickTerminal: (part: Part, terminal: number) => void;
    clickWire: (wire: Wire) => void;
    disconnectWire: (wireId: number, end: number) => void;
    mouseMoveHandler?: (x: number, y: number) => void;
    delete: () => void;
    simplify: () => void;
    step: () => void;
}

interface State {
    stateId: number;
    run: boolean;
    dragDraft?: boolean;
    parts: Map<number, Part>; //{ [id: number]: Part };
    wires: Map<number, Wire>;
    network?: Network;
    editingPartId?: number;
    movingPartId?: number;
    selectedPartId?: number;
    hoveredPartId?: number;
    selectedWireId?: number;
    highlightTerminal?: number;
    editingWireId?: number;
    editingWireEnd?: StartOrEnd;
    system?: SystemOfEquations;
    solution?: SystemOfEquations;
    eventHandlers: EventHandlers
}

const voltageSourceType = new VoltageSourceType();
const resistorType = new ResistorType();
const capacitorType = new CapacitorType();
const diodeType = new DiodeType();
const snapSize = 20;

function snap(d: number) {
    return Math.round(d / snapSize) * snapSize;
}

function setResistance(part: Part, resistance: number) {
    (part.properties as ResistorProperties).resistance = resistance;
    return part;
}

function createParts() {
    const parts: Map<number, Part> = new Map(); //{ [id: number]: Part } = {};
    function addPart(p: Part) { parts.set(p.id, p); }
    addPart(new Part(voltageSourceType, 60, 80));
    addPart(setResistance(new Part(resistorType, 260, 80), 20));
    addPart(new Part(resistorType, 120, 220));
    addPart(setResistance(new Part(resistorType, 260, 220), 50));
    addPart(new DummyTerminalPart(60, 260));
    //addPart(new Part(diodeType, 200, 320));

    const wires: Map<number, Wire> = new Map();
    function addWire(w: Wire) { wires.set(w.id, w); }
    addWire(new Wire(new PartTerminal(0, 0), new PartTerminal(1, 0)));
    addWire(new Wire(new PartTerminal(1, 1), new PartTerminal(3, 0)));
    addWire(new Wire(new PartTerminal(3, 0), new PartTerminal(2, 0)));
    addWire(new Wire(new PartTerminal(3, 1), new PartTerminal(2, 1)));
    addWire(new Wire(new PartTerminal(2, 1), new PartTerminal(4, 0)));
    addWire(new Wire(new PartTerminal(4, 0), new PartTerminal(0, 1)));

    return {
        parts,
        wires
    };
}
const deltaT = 0.005;

function finishEdit(state: State, notChanged?: boolean) {
    const t0 = Date.now();
    const previousNetwork = state.network;
    if (notChanged !== true || !state.network) {
        dropExtraDummyTerminals(state.parts, state.wires);
        state.network = buildNet(state.parts, state.wires);
    }

    //network.log();
    const t1 = Date.now();
    const system = formSystemOfEquations(deltaT, state.parts, state.wires, state.network!, previousNetwork, state.solution);
    const solution = solve(system);
    const t2 = Date.now();

    solution.updateModel(state.network!, state.parts, state.wires);

    const t3 = Date.now();

    //console.log(`Network ${t1 - t0} ms, solving ${t2 - t1} ms, updating model ${t3 - t2} ms`);

    state.system = system;
    state.solution = solution;
}

const createEventHandlers = (set: any, pause: any, resume: any): EventHandlers => ({
    start: () => set(
        produce((state: State) => {
            finishEdit(state);
        })
    ),
    pauseOrResume: () => set(
        produce((state: State) => {
            state.run = !state.run;
        })
    ),
    step: () => set(
        produce((state: State) => {
            if (state.solution) {
                finishEdit(state, true);
            }
        })
    ),
    simplify: () => set(
        produce((state: State) => {
            if (state.system) {
                state.system = simplify(state.system, true);
            }
        })
    ),
    delete: () => set(
        produce((state: State) => {
            if (state.selectedPartId !== undefined) {
                const partId = state.selectedPartId;
                const part = state.parts.get(partId);
                if (part) {
                    state.parts.delete(partId);
                    state.wires.forEach((wire, wireId) => {
                        if (wire.start.partId === partId) {
                            const [x, y] = part.getTerminalCoordinates(wire.start.terminalId);
                            const dummy = new DummyTerminalPart(x, y, false);
                            state.parts.set(dummy.id, dummy);
                            wire.start = new PartTerminal(dummy.id, 0);
                        }
                        if (wire.end.partId === partId) {
                            const [x, y] = part.getTerminalCoordinates(wire.end.terminalId);
                            const dummy = new DummyTerminalPart(x, y, false);
                            state.parts.set(dummy.id, dummy);
                            wire.end = new PartTerminal(dummy.id, 0);
                        }
                    });
                    state.selectedPartId = undefined;
                }
            } else if (state.selectedWireId !== undefined) {
                state.wires.delete(state.selectedWireId);
                state.selectedWireId = undefined;
            }

            finishEdit(state);
        })
    ),
    click: (x: number, y: number) => set(
        produce((state: State) => {
            if (state.editingWireId !== undefined) {
                state.editingWireId = undefined;
                finishEdit(state);
            } else if (state.movingPartId !== undefined) {
                state.movingPartId = undefined;
                finishEdit(state);
            }
            state.selectedPartId = undefined;
            state.selectedWireId = undefined;
        })
    ),
    addPart: (type: string) => set(
        produce((state: State) => {
            if (type === 'resistor') {
                const newPart = new Part(resistorType, 50, 20);
                state.parts.set(newPart.id, newPart);
                state.movingPartId = newPart.id;
            } else if (type === 'capacitor') {
                const newPart = new Part(capacitorType, 50, 20);
                state.parts.set(newPart.id, newPart);
                state.movingPartId = newPart.id;
            } else if (type === 'diode') {
                const newPart = new Part(diodeType, 50, 20);
                state.parts.set(newPart.id, newPart);
                state.movingPartId = newPart.id;
            }
        })
    ),
    editPart: (part?: Part) => set(
        produce((state: State) => {
            state.editingPartId = part ? part.id : undefined;
        })
    ),
    setPartProperty: (partId: number, property: string, newValue: string) => set(
        produce((state: State) => {
            const part = state.parts.get(partId)!;
            const newFloatValue = parseFloat(newValue);
            console.log(`Setting part property on ${partId} / ${property} = ${newFloatValue}`);
            const newProperties = { ...part.properties };
            (newProperties as any)[property] = newFloatValue;
            part.properties = newProperties;

            // TODO Would be better to set all props of the part at once
            finishEdit(state);
        })
    ),
    selectPart: (part: Part) => set(
        produce((state: State) => {
            if (state.movingPartId !== undefined) {
                state.movingPartId = undefined;
                finishEdit(state);
            } else {
                state.selectedPartId = part.id;
                state.selectedWireId = undefined
            }
        })
    ),
    movePart: (part: Part, x: number, y: number, draft: boolean) => {
        //console.log(`movePart, draft = ${draft}`);
        if (draft) {
            //pause();
        }
        set(
            produce((state: State) => {
                state.dragDraft = draft;
                const target = state.parts.get(part.id)!;
                target.x = snap(x);
                target.y = snap(y);
                if (!draft) {
                    finishEdit(state);
                }
            })
        );
        if (!draft) {
            //resume();
        }
    },
    hoverTerminal: (part: Part, terminal?: number) => {
        pause();
        set(
            produce((state: State) => {
                state.hoveredPartId = part.id;
                state.highlightTerminal = terminal;
            })
        );
        resume();
    },
    disconnectWire: (wireId: number, startOrEnd: StartOrEnd) => set(
        produce((state: State) => {
            if (wireId === state.editingWireId) return;
            const wire = state.wires.get(wireId)!;
            const partId = startOrEnd === StartOrEnd.start ? wire.start.partId : wire.end.partId;
            const part = state.parts.get(partId);
            if (part) {
                const [dx, dy] = wire.getDisconnectCoords(
                    startOrEnd,
                    (partTerminal) => state.parts.get(partTerminal.partId)!.getTerminalCoordinates(partTerminal.terminalId)
                )
                const dummy = new DummyTerminalPart(dx, dy, false);
                state.parts.set(dummy.id, dummy);
                if (startOrEnd === StartOrEnd.start) {
                    wire.start = new PartTerminal(dummy.id, 0);
                } else {
                    wire.end = new PartTerminal(dummy.id, 0);
                }

                finishEdit(state);
            }
        })
    ),
    mouseMoveHandler: (x: number, y: number) => set(
        produce((state: State) => {
            if (state.editingWireId !== undefined) {
                const wire = state.wires.get(state.editingWireId);
                if (wire) {
                    const partId = state.editingWireEnd === StartOrEnd.start ? wire.start.partId : wire.end.partId;
                    const dummy = state.parts.get(partId);
                    if (dummy) {
                        dummy.x = snap(x);
                        dummy.y = snap(y);
                    }
                }
            } else if (state.movingPartId !== undefined) {
                const target = state.parts.get(state.movingPartId)!;
                target.x = snap(x);
                target.y = snap(y);
            }
        })
    ),
    clickTerminal: (part: Part, terminal: number) => set(
        produce((state: State) => {
            //console.log('clickTerminal');
            if (state.editingWireId === undefined) {
                const terminalCoords = part.getTerminalCoordinates(terminal);
                const dummy = new DummyTerminalPart(...terminalCoords, true);
                state.parts.set(dummy.id, dummy);
                const wire = new Wire(new PartTerminal(part.id, terminal), new PartTerminal(dummy.id, 0));
                state.wires.set(wire.id, wire);
                state.editingWireId = wire.id;
                state.editingWireEnd = 1;
            } else {
                const wire = state.wires.get(state.editingWireId);
                if (wire) {
                    if (state.editingWireEnd === StartOrEnd.start) {
                        wire.start = new PartTerminal(part.id, terminal);
                    } else {
                        wire.end = new PartTerminal(part.id, terminal);
                    }
                    state.editingWireId = undefined;

                    finishEdit(state);
                }
            }
        })
    ),
    clickWire: (wire: Wire) => set(
        produce((state: State) => {
            state.selectedPartId = undefined;
            state.selectedWireId = wire.id;
        })
    )
});

const useStoreWithUndo = create<State>()(
    temporal(
        (set) => ({
            stateId: 0,
            run: false,
            ...createParts(),
            eventHandlers: (null as unknown) as EventHandlers
        }),
        {
            equality: (pastState, currentState) => {
                if (currentState.dragDraft) {
                    //console.log('EQUAL');
                    return true;
                }
                return isDeepEqual(pastState, currentState)
            },
        }
    )
);

function useStore(pickFunction?: (state: State) => any) {
    return useStoreWithUndo(
        useShallow(state => {
            if (pickFunction) {
                return pickFunction(state);
            }
            return state;
        })
    );
}

const temporalProperties = useStoreWithUndo.temporal;
const setState = useStoreWithUndo.setState;
const getState = useStoreWithUndo.getState;

const { undo, redo, pause, resume } = temporalProperties.getState();
const state = getState();
if (!state.eventHandlers) {
    const eventHandlers = createEventHandlers(setState, pause, resume);
    setState({
        ...state,
        eventHandlers
    });
    console.log('Initializing event handlers');
    eventHandlers.start();
}


export { type EventHandlers, type State, useStore, createEventHandlers, temporalProperties, setState, snap };
