
class DataMaps {
    wireCurrents: Map<number, number>;
    wireVoltages: Map<number, number>;
    partTerminalCurrents: Map<string, number>;
    partTerminalVoltages: Map<string, number>;
    
    constructor() {
        this.wireCurrents = new Map();
        this.wireVoltages = new Map();
        this.partTerminalCurrents = new Map();
        this.partTerminalVoltages = new Map();
    }

    clear() {
        this.wireCurrents.clear();
        this.wireVoltages.clear();
        this.partTerminalCurrents.clear();
        this.partTerminalVoltages.clear();
    }
}

// Circuit data like voltages and currents are held here instead of in the React-managed state,
// because we don't want to rerender the entire circuit when the data changes. Instead, we only
// update the relevant information in an animation hook.
const CircuitData = new DataMaps();

export default CircuitData;
