import { Group, Line, Circle, Text } from 'react-konva';
import { vec } from '@josh-brown/vector';
import { EventHandlers } from '../store/MainStore';
import { getWireDisconnectArrowCoordinates } from '../util';
import { Line as KonvaLine } from 'konva/lib/shapes/Line';
import Part from '../model/Part';
import Wire from '../model/Wire';
import Constants from '../util/Constants';
import { formatCurrent, formatCurrentAndArrow, formatVoltage } from '../util/Formatting';

function renderWire(parts: Map<number, Part>, wire: Wire, selected: boolean, eventHandlers: EventHandlers) {
    const startPart = parts.get(wire.start.partId);
    const endPart = parts.get(wire.end.partId);
    if (!startPart) {
        throw new Error(`Part with id ${wire.start.partId} not found!`);
    }
    if (!endPart) {
        throw new Error(`Part with id ${wire.end.partId} not found!`);
    }
    const [x1, y1] = startPart.getTerminalCoordinates(wire.start.terminalId);
    const [x2, y2] = endPart.getTerminalCoordinates(wire.end.terminalId);

    const disconnectArrowCoords1 = getWireDisconnectArrowCoordinates(vec([x2, y2]), vec([x1, y1]));
    const disconnectArrowCoords2 = getWireDisconnectArrowCoordinates(vec([x1, y1]), vec([x2, y2]));
    const drawArrow = x1 !== x2 || y1 !== y2;
    const showHideArrow = (line: KonvaLine, show: boolean) => {
        line.fill(show ? 'red' : 'transparent');
        line.stroke(show ? 'red' : 'transparent');
    };
    const selectedColor = Constants.rendering.selectedWireColor;
    const flipArrow = x2 < x1;
    const rotation = (x1 === x2 && y1 === y2) ? 0 : Math.atan((y2 - y1) / (x2 - x1)) / Math.PI * 180;
    const voltsAndAmpsFontSize = Constants.rendering.voltsAndAmpsFontSize;

    return (
        <Group key={wire.id}>
            {selected && <Line points={[x1, y1, x2, y2]} stroke={selectedColor} strokeWidth={7} />}
            <Line points={[x1, y1, x2, y2]} stroke={'black'} strokeWidth={2} />
            <Line
                points={[x1, y1, x2, y2]}
                stroke='transparent'
                strokeWidth={5} dash={[5, 10, 5, 10]} 
                name='wire' id={'' + wire.id} />
            <Line points={[x1, y1, x2, y2]} stroke='transparent' strokeWidth={10}
                onClick={(e) => { eventHandlers.clickWire(wire); e.cancelBubble = true; }} />
            <Group x={(x1 + x2) / 2} y={(y1 + y2) / 2} rotation={rotation}>
                <Text x={-30} y={-12} width={60} align='center' fill='gray' fontSize={voltsAndAmpsFontSize}
                    text={formatVoltage(wire.voltage)}
                    name='wireVoltage' id={'' + wire.id} />
                <Text x={-30} y={5} width={60} align='center' fill='gray' fontSize={voltsAndAmpsFontSize}
                    text={formatCurrentAndArrow(wire.current, flipArrow)} 
                    name='wireCurrent' id={(flipArrow ? '_' : '') + wire.id} />
            </Group>
            <Circle x={x1} y={y1} radius={2} strokeWidth={1} stroke='black' fill='black' />
            <Circle x={x2} y={y2} radius={2} strokeWidth={1} stroke='black' fill='black' />
            {drawArrow ?
                <Line points={disconnectArrowCoords1} stroke='transparent' fill='transparent' closed
                    onMouseOver={(e) => showHideArrow(e.target as KonvaLine, true)}
                    onMouseOut={(e) => showHideArrow(e.target as KonvaLine, false)}
                    onClick={(e) => { eventHandlers.disconnectWire(wire.id, 0); e.cancelBubble = true; }} />
                : ''}
            {drawArrow ?
                <Line points={disconnectArrowCoords2} stroke='transparent' fill='transparent' closed
                    onMouseOver={(e) => showHideArrow(e.target as KonvaLine, true)}
                    onMouseOut={(e) => showHideArrow(e.target as KonvaLine, false)}
                    onClick={(e) => { eventHandlers.disconnectWire(wire.id, 1); e.cancelBubble = true; }} />
                : ''}
        </Group>
    );
}

export default renderWire;
