import { Group, Line, Text } from 'react-konva';
import TwoTerminalBaseType from './TwoTerminalBaseType';
import Part from '../model/Part';
import { SystemBuilder } from '../simulation/SystemBuilder';

interface DiodeProperties {
    forwardVoltage: number;
}

class DiodeType extends TwoTerminalBaseType<DiodeProperties> {
    constructor() {
        super('diode', 2);
    }

    renderSymbol(color: string) {
        const xx = 15;
        return (
            <Group>
                <Line points={[0, -38, 0, -10]} stroke={color} />
                <Line points={[-xx, 10, xx, 10, 0, -10]} stroke={color} closed={true} />
                <Line points={[-xx, -10, xx, -10]} stroke={color} />
                <Line points={[0, 10, 0, 38]} stroke={color} />
            </Group>
        );
    }

    renderDetails(x: number, y: number, align: string, properties: DiodeProperties): React.ReactNode {
        return (
            <Text x={x} y={y} width={100} fontSize={18} align={align} text={properties.forwardVoltage + ' V'} />
        );
    }

   createDefaultProperties(): DiodeProperties {
        return {
            forwardVoltage: 0.6
        };
    }

    getPropertyLabels() {
        return {
            forwardVoltage: {
                label: 'Forward voltage',
                help: '...'
            }
        };
    }

    getName(): string {
        return 'diode';
    }

    buildSystem(part: Part, builder: SystemBuilder): void {
        // TODO
    }
}

export default DiodeType;
