import { Group, Line, Text } from 'react-konva';
import TwoTerminalBaseType from './TwoTerminalBaseType';
import { PropertyLabels } from './PartTypeUtil';
import Part from '../model/Part';
import { Network } from '../simulation/Network';
import { SystemBuilder } from '../simulation/SystemBuilder';
import PartTerminal from '../model/PartTerminal';
import { NetVariable, PartTerminalVariable } from '../simulation/Variable';
import { Current, NA, Voltage } from '../simulation/PhysicalProperty';
import { Constant } from '../simulation/Value';

interface VoltageSourceProperties {
    voltage: number;
}

class VoltageSourceType extends TwoTerminalBaseType<VoltageSourceProperties> {
    constructor() {
        super('voltageSource', 2);
    }

    renderSymbol(color: string) {
        const xx = 20;
        const yy = 3;
        return (
            <Group>
                <Line points={[0, -38, 0, -yy]} stroke={color} />
                <Line points={[-xx, -yy, xx, -yy]} stroke={color} />
                <Line points={[-xx / 2, yy, xx / 2, yy]} stroke={color} />
                <Line points={[0, yy, 0, 38]} stroke={color} />
            </Group>
        );
    }

    renderDetails(x: number, y: number, align: string, properties: VoltageSourceProperties): React.ReactNode {
        return (
            <Text x={x} y={y} width={100} fontSize={18} align={align} text={properties.voltage + ' V'} />
        );
    }

    createDefaultProperties(): VoltageSourceProperties {
        return {
            voltage: 5.0
        }
    }

    getPropertyLabels(): PropertyLabels<VoltageSourceProperties> {
        return {
            voltage: {
                label: "Voltage",
                help: "Voltage in volts"
            }
        }
    }

    getName(): string {
        return 'voltage source';
    }

    buildSystem(part: Part, builder: SystemBuilder): void {
        const pt0 = new PartTerminal(part.id, 0);
        const pt1 = new PartTerminal(part.id, 1);
        const net0 = builder.network.getNetId(pt0);
        const net1 = builder.network.getNetId(pt1);
        const v0 = new NetVariable(Voltage, net0);
        const v1 = new NetVariable(Voltage, net1);
        const current0 = new PartTerminalVariable(Current, pt0);
        const current1 = new PartTerminalVariable(Current, pt1);
        const voltage = (part.properties as VoltageSourceProperties).voltage;
        builder.addRow([builder.addVariable(v0)], [1], new Constant(Voltage, voltage));
        // TODO To allow multiple voltage sources, should not set v1 = 0 but instead define the
        // difference of v0 and v1
        builder.addRow([builder.addVariable(v1)], [1], new Constant(Voltage, 0));
        if (builder.network.hasWires(pt0) && builder.network.hasWires(pt1)) {
            builder.addRow([builder.addVariable(current0),builder. addVariable(current1)], [1, 1], new Constant(NA, 0));
        } else {
            builder.addRow([builder.addVariable(current0)], [1], new Constant(NA, 0));
            builder.addRow([builder.addVariable(current1)], [1], new Constant(NA, 0));
        }
    }
}

export { VoltageSourceType, type VoltageSourceProperties };
