import { Button, Form, Modal } from "react-bootstrap";
import { State, useStore } from "./store/MainStore";

const PropertyDialog = () => {
    const { parts, editingPartId, eventHandlers }: State = useStore(state => 
        ({ parts: state.parts, editingPartId: state.editingPartId, eventHandlers: state.eventHandlers })
    );

    if (editingPartId === undefined) {
        return <></>;
    }
    const part = parts.get(editingPartId)!;
    const newProperties = { ...part.properties };
    const propertyLabels = part.type.getPropertyLabels();
    const onChange = (property: string, newValue: string) => {
        //eventHandlers.setPartProperty(part.id, property, newValue);
        (newProperties as any)[property] = newValue;
    };
    const save = () => {
        console.log('save');
        Object.entries(newProperties).forEach(([property, value]) => {
            eventHandlers.setPartProperty(part.id, property, value as string);
        });
        eventHandlers.editPart();
    };
    const cancel = () => {
        eventHandlers.editPart();
    };
    const fieldIdPrefix = window.crypto.randomUUID();

    return (
        <Modal show={true}
            onShow={() => document.getElementById(fieldIdPrefix + '_0')?.focus()}
            onEscapeKeyDown={(e) => cancel()}>
            <Modal.Header closeButton>
                <Modal.Title>Edit {part.type.getName()}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form onSubmit={(e) => { console.log('s'); e.preventDefault(); save(); }}>
                    {Object.entries(part.properties || []).map(([property, value], index) => (
                        <Form.Group key={property} className="mb-3">
                            <Form.Label>{(propertyLabels as any)[property].label}</Form.Label>
                            <Form.Control type="text"
                                id={fieldIdPrefix + '_' + index}
                                onChange={(e) => onChange(property, e.target.value)}
                                defaultValue={value as any} />
                            <Form.Text className="text-muted">
                                {(propertyLabels as any)[property].help}
                            </Form.Text>
                        </Form.Group>
                    ))}
                </Form>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={(e) => cancel()}>Cancel</Button>
                <Button variant="primary" onClick={(e) => save()}>Save changes</Button>
            </Modal.Footer>
        </Modal>
    );
}

export default PropertyDialog;
