import { immerable } from "immer";
import { vec } from '@josh-brown/vector';
import { Vec2 } from "../util";
import PartTerminal from "./PartTerminal";
import Entity from "./Entity";

enum StartOrEnd {
    'start',
    'end'
}
var idCounter = 0;

class Wire extends Entity {
    [immerable] = true

    start: PartTerminal;
    end: PartTerminal;
    id: number;
    current = 0;
    voltage = 0;

    constructor(start: PartTerminal, end: PartTerminal, id?: number) {
        super();
        this.start = start;
        this.end = end;
        this.id = id ?? (idCounter++);
    }

    getDisconnectCoords(startOrEnd: StartOrEnd, partLookup: (partTerminal: PartTerminal) => [x: number, y: number]): Vec2 {
        let p, v;
        if (startOrEnd === StartOrEnd.start) {
            p = vec(partLookup(this.start));
            v = p.add(vec(partLookup(this.end)).scalarMultiply(-1));
        } else {
            p = vec(partLookup(this.end));
            v = p.add(vec(partLookup(this.start)).scalarMultiply(-1));
        }
        const unit = v.scalarMultiply(1 / Math.sqrt(v.innerProduct(v)));
        const result = p.add(unit.scalarMultiply(-20)).toArray();
        return [Math.round(result[0]), Math.round(result[1])];
    }
}

export default Wire;
export { StartOrEnd };
