import { Matrix, vec, Vector } from "@josh-brown/vector";

type NMatrix = Matrix<number>;
type NVector = Vector<number>;

function forwardSubstitute(a: NMatrix, b: NVector): NVector {
    const result: number[] = [];
    for (var row = 0; row < a.getNumberOfRows(); row++) {
        const coefficient = b.getEntry(row);
        var sum = 0;
        for (var col = 0; col < row; col++) {
            sum += a.getEntry(row, col) * result[col];
        }
        result[row] = (coefficient - sum) / a.getEntry(row, row);
    }
    return vec(result);
}

function backwardSubstitute(a: NMatrix, b: NVector): NVector {
    const result: number[] = [];
    const n = a.getNumberOfRows();
    for (var row = n - 1; row >= 0; row--) {
        const coefficient = b.getEntry(row);
        var sum = 0;
        for (var col = row + 1; col < n; col++) {
            sum += a.getEntry(row, col) * result[col];
        }
        result[row] = (coefficient - sum) / a.getEntry(row, row);
    }
    return vec(result);
}

function fill(arr: number[], n: number) {
    const result = [...arr];
    while (result.length < n) {
        result.push(0);
    }
    return result;
}

function isNumber(x: any): boolean {
    return typeof x === 'number' && isFinite(x);
}

export { type NMatrix, type NVector, forwardSubstitute, backwardSubstitute, fill, isNumber };
