import { immerable } from "immer";
import Part from "./Part";
import DummyTerminalType from "../parttype/DummyTerminalType";

class DummyTerminalPart extends Part {
    [immerable] = true

    dragging: boolean;

    constructor(x: number, y: number, dragging?: boolean) {
        super(new DummyTerminalType(), x, y);
        this.dragging = dragging || false;
    }
}

export default DummyTerminalPart;
