function formatVoltage(v: number) {
    return `${Math.round(v * 100) / 100}V`;
}

function formatCurrent(c: number) {
    return `${Math.round(c * 1000)}mA`;
}

function formatCurrentAndArrow(current: number, flipArrow: boolean) {
    const arrowSymbol = current === 0
        ? ''
        : (current < 0 && flipArrow ? '⇦' : '⇨');
    return arrowSymbol + ' ' + formatCurrent(Math.abs(current));
}

function formatPower(p: number) {
    return `${Math.round(p * 1000)}mW`;
}

function voltageToColor(v: number) {
    if (isNaN(v) || v === null) {
        return 'black';
    }
    const r = Math.round(255 / 5 * v);
    const gb = Math.round(60 / 5 * v);
    return `rgb(${r}, ${gb}, ${gb})`;
}

const ctx: CanvasRenderingContext2D = document.createElement('canvas').getContext('2d')!;

function createVoltageGradient(v0: number, v1: number, x0: number, y0: number, x1: number, y1: number): CanvasGradient {
    const gradient = ctx.createLinearGradient(x0, y0, x1, y1);
    gradient.addColorStop(0.00, voltageToColor(v0));
    gradient.addColorStop(1.00, voltageToColor(v1));
    return gradient;
}

export { formatVoltage, formatCurrent, formatCurrentAndArrow, formatPower, voltageToColor, createVoltageGradient };
